}

.c-header__tldr a:after {
  content: 'tl;dr';
}

.c-header__tldr--close a:after {
  content: 'close';
}

.c-header__trigger {
  width: 26px;
  height: 20px;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  transition: 0.3333333333s;
  cursor: pointer;
  position: relative;
  margin-top: 21px;
  float: right;
  z-index: 500;
}

.c-header__trigger .patty {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: #0a0a0a;
  opacity: 1;
  left: 0;
  margin-bottom: -10px;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  transition: all 0.3333333333s;
}

.c-header__trigger .patty:nth-child(1) {
  top: 0;
  -webkit-transform-origin: left center;
  transform-origin: left center;
}

.c-header__trigger .patty:nth-child(2) {
  top: 8px;
  -webkit-transform-origin: left center;
  transform-origin: left center;
}

.c-header__trigger .patty:nth-child(3) {
  top: 16px;
  -webkit-transform-origin: left center;
  transform-origin: left center;
}

.c-header__trigger--close .patty {
  background: #34ffe0;
}

.c-header__trigger--close .patty:nth-child(1) {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  top: -6px;
  left: 0;
}

.c-header__trigger--close .patty:nth-child(2) {
  width: 0;
  opacity: 0;
}

.c-header__trigger--close .patty:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  top: 12px;
  left: 0;
}

.c-overlay {
  position: fixed;
  width: 100%;
  min-height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: scroll;
  z-index: 9;
  padding-top: 8em;
}

@media (max-width: 41.1011111111em) {
  .c-overlay {
    padding-top: 6em;
  }
}

.c-overlay--effect {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3333333333s, visibility 0.3333333333s;
}

.c-overlay--effect.open {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3333333333s;
}

.c-overlay__lead {
  font-family: "SuisseIntl Regular", "Helvetica Neue", sans-serif;
}

.c-overlay__info {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  display: block;
  width: 100%;
  margin-top: 80px;
  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
}

@media (max-width: 41.1011111111em) {
  .c-overlay__info {
    position: relative;
    margin-bottom: 0px !important;
  }
}

.c-overlay__arrow-btn {
  background-image: url("../images/svg/arrow-right-huge.svg") !important;
  background-size: 123px 65px;
  width: 123px;
  height: 65px;
  display: inline-block;
  transition: all 0.3333333333s;
}

@media (max-width: 41.1011111111em) {
  .c-overlay__arrow-btn {
    background-size: 66px 35px;
    width: 66px;
    height: 35px;
  }
}

.c-overlay__arrow-btn--small {
  background-size: 66px 35px;
  width: 66px;
  height: 35px;
}

.c-overlay__arrow-btn:hover {
